.expence-item{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.5rem;
    margin: 1rem 0;
    background-color: #b16363;
}
.expence-item__description{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: flex-end;
    flex-flow: column-reverse;
    justify-content: flex-start;
    flex:1;
}
.expence-item h2{
    color: #3a3a3a;
    font-size: 1rem;
    flex: 1;
    margin: 0 1rem;
    color: white;
}
.expence-item__price{
    font-size: 1rem;
    font-weight: bold;
    color: white;
    background-color: #40005d;
    border: 1px solid white;
    padding: 0.5rem;
    border-radius: 12px;   
}
@media (min-width:580px){
    .expence-item__description{
        flex-direction: row;
        align-items: center;
        justify-content: flex-start;
        flex:1;
    }
    .expence-item__description h2{
        font-size: 1.25rem;
    }
    .expence-item__price{
        font-size: 1.25rem;
        padding: 0.5rem 1.5rem;
    }
}
